























































































































































@import '@design';

.wrapper {
  background-color: white;
  box-shadow: 0 2px 8px #f0f1f2;
  border-radius: $size-task-border-radius;
  margin-bottom: 15px;
  position: relative;
  overflow: hidden;
  &--break {
    .timer {
      color: white;
    }
    .dot {
      transform: scale(100);
      background-color: $color-scondary;
      transition: all 1s cubic-bezier(0.13, 0.56, 0.59, 0.96) 0.5s;
    }
  }
}
.content {
  padding: 25px;
  text-align: center;
  z-index: 1;
}

.tomato-list {
  padding: 0;
  margin-bottom: 0;
}
.tomato {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: $color-primary;
  display: inline-block;
  margin: 0 5px;
}
.controls {
  position: relative;
  .button {
    &--side {
      position: absolute;
      top: 50%;
      &:first-child {
        left: 50%;
        transform: translate(-60px, -50%);
      }
      &:last-child {
        right: 50%;
        transform: translate(60px, -50%);
      }
    }
  }
}
.timer {
  transition: color 1.5s;
}
.dot {
  position: absolute;
  left: 50%;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: $color-primary;
  bottom: 25px;
  transform: translateX(-50%);
  transition: all 1s cubic-bezier(0.13, 0.56, 0.59, 0.96) 0.5s;
  z-index: 0;
}
