



















































































































































































































































@import '@design';
.task-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  background-color: white;
  border-radius: $size-task-border-radius;
  margin-bottom: 10px;
}
:global {
  .task-prefix,
  .task-suffix {
    color: #bfbfbf;
    font-size: 1.25rem;
  }
  .task-prefix {
    padding-right: 10px;
  }
  .task-suffix {
    padding-left: 10px;
  }
}
